'use server';

import { revalidatePath } from 'next/cache';
import { decrementTournamentSlot, getTournamentById } from './data';

export async function handleSuccessfulBookingAction(tournamentId: string) {
  try {
    console.log(`Server Action: Attempting to decrement slot for ${tournamentId}`);
    decrementTournamentSlot(tournamentId); // This mutates the server-side 'mockTournaments'

    const tournament = getTournamentById(tournamentId); // Get updated tournament data
    
    // Revalidate paths to ensure fresh data is fetched for subsequent navigations
    revalidatePath('/tournaments');
    revalidatePath(`/tournaments/${tournamentId}`);
    // Revalidating specific sub-pages might also be beneficial depending on flow
    // revalidatePath(`/tournaments/${tournamentId}/book`); 
    // revalidatePath(`/tournaments/${tournamentId}/payment`);

    console.log(`Server Action: Slot decremented for ${tournamentId}. New server-side count: ${tournament?.availableSlots}`);

    return { success: true, message: 'Slot booked successfully and count updated.', newSlotCount: tournament?.availableSlots };
  } catch (error) {
    console.error("Server Action Error in handleSuccessfulBookingAction:", error);
    return { success: false, error: "Failed to update slot count on the server.", newSlotCount: -1 };
  }
}
